import Vue from 'vue'
import axios from 'axios'


export function request (config ){

  const instance = axios.create({
    baseURL: process.env.VUE_APP_API_BASE,
    timeout: 5000,
    withCredentials: true,
    headers: {
        "x-requested-with": "xmlhttprequest"
    }
  });
  
  instance.interceptors.response.use( res => {
    var obj = res.data;
    if (typeof obj.rtn != "undefined" && parseInt(obj.rtn) >= 90){
        obj.msg && console.error(obj.msg);
        return top.location = process.env.VUE_APP_API_BASE + "/subway/login?target=" + escape(window.location.href);
    }      
    return res.data;
  }, err => {
    return err;
  })
  
  return instance(config);
}


