import Vue from 'vue'
import Router from 'vue-router'



const Home = () => import('./views/Home.vue')

const Bat = () => import('./views/Bat.vue')
const batIndex = () => import('./views/bat/batIndex.vue')
const timeCreate = () => import('./views/bat/timeCreate.vue')
const timeApply = () => import('./views/bat/timeApply.vue')
const areaCreate = () => import('./views/bat/areaCreate.vue')
const areaApply = () => import('./views/bat/areaApply.vue')
const peopleCreate = () => import('./views/bat/peopleCreate.vue')
const peopleApply = () => import('./views/bat/peopleApply.vue')


Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    { 
      path: '/', 
      redirect: '/home' ,
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
	
    {
      path: '/bat',
      name: 'bat',
      component: Bat,
      
      children: [
        // 当 /user/:id 匹配成功，
        // UserHome 会被渲染在 User 的 <router-view> 中
        { path: '/bat', component: batIndex },
        { path: '/bat/timeCreate', component: timeCreate },
        { path: '/bat/timeApply', component: timeApply },
        { path: '/bat/areaCreate', component: areaCreate },
        { path: '/bat/areaApply', component: areaApply },
        { path: '/bat/peopleCreate', component: peopleCreate },
        { path: '/bat/peopleApply', component: peopleApply },
      ]
    },
    {
      path: '/help',
      name: 'help',
      component: () => import(/* webpackChunkName: "about" */ './views/Help.vue')
    },
    {
      path: '/auto',
      name: 'auto',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Auto.vue')
    }
  ]
})
