import {
  INCREMENT,
  ADD_SELECTED_GOODS,
  REMOVE_SELECTED_GOODS
} from './mutations-types.js'

export default {
  
  [INCREMENT](state) {
    state.counter++;
  },
  [ADD_SELECTED_GOODS](state, payload){
    state.SelectedGoodsList.push(payload);
  },  
  [REMOVE_SELECTED_GOODS](state, payload){
    state.SelectedGoodsList = state.SelectedGoodsList.filter((item) => item.GoodsNo !== payload.GoodsNo);
  }
}

