import {
  INCREMENT,
  ADD_SELECTED_GOODS,
  REMOVE_SELECTED_GOODS
} from './mutations-types.js'


export default {

  addSelectedGoods(context, payload){
    
    var product = context.state.SelectedGoodsList.find(item => {
      
      return item.GoodsNo === payload.GoodsNo;
    })  
    if(!product)
    {
      context.commit(ADD_SELECTED_GOODS,payload)
    } else {
      context.commit(REMOVE_SELECTED_GOODS,payload)
    }
  }
}